.app_navbar {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  background: var(--color-black);
  padding: 1rem 2rem;
}
.app__navbar-logo{
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.app__navbar-logo img{
    width: 150px;
}
.app__navber-links{
    flex: 1;
    display: flex;
    justify-content: center;
    align-items: center;
    list-style: none;
    
}
.app__navber-links li{
    margin: 0 1rem;
    cursor: pointer;
    letter-spacing: 0.03rem;
    letter-spacing: 4px;
    transition: .3s ease-in-out;
    text-transform: uppercase;
}
.app__navber-links li:hover{
    color: var(--color-gray);
}
.app__navbar-login{
    display: flex;
    justify-content: flex-end;
    align-items: center;

}
.app__navbar-login a{
    margin: 0 1rem;
    text-decoration: none;
    transition: 0.5s ease;
}
.app__navbar-login a:hover{
    border-bottom: 1px solid var(--color-golden);
}
.app__navbar-login div{
    width: 1px;
    height: 30px;
    background-color: var(--color-gray);
}
.app__navbar-smallscreen{
    display: none;
}
.app__navbar-smallScreen_overlay{
    position: fixed;
    left: 0;
    width: 100%;
    height: 100vh;
    background: var(--color-black);
    transition: 0.5s ease;
    flex-direction: column;
    z-index: 5;
}
.app__navbar-smallScreen_overlay .overlay__close{
    font-size: 27px;
    color: var(--color-golden);
    cursor: pointer;
    position: absolute;
    top: 20px;
    right: 20px;

}
.app__navber-smallscreen-links{
    list-style: none;
}
.app__navber-smallscreen-links li{
    margin: 2rem;
    cursor: pointer;
    color: var(--color-golden);
    font-size: 2rem;
    text-align: center;
    font-family: var(--font-base);
    letter-spacing: 2px;
    transition: .3s ease-in-out;
    text-transform: uppercase;
}
.app__navber-smallscreen-links li:hover{
    color: var(--color-white);
}
@media screen and (max-width:2000px){
.app__navbar-logo img{
    width: 210px;
}
}

@media screen and (max-width:1150px){
    .app__navber-links{
        display: none;
    }
    .app__navbar-smallscreen{
        display: flex;
    }
}
@media screen and (max-width:650px){
.app_navbar{
    padding: 1rem;

}
.app__navbar-login{
    display: none;
}
.app__navbar-logo img{
    width: 150px;
}
}