.app__footer {
  width: 100%;
  position: relative;
  z-index: 1;

  display: flex;
  justify-content: flex-start;
  align-items: center;
  flex-direction: column;

  background: var(--color-black);
  padding-top: 0;
}

.app__footer-links {
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: flex-start;

  margin-top: 5rem;
  padding: 0 2rem;
}

.app__footer-links_contact,
.app__footer-links_logo,
.app__footer-links_work {
  flex: 1;
  margin: 1rem;
  text-align: center;
}

.app__footer-links_logo img:first-child {
  width: 210px;
  margin-bottom: 0.75rem;
}

.app__footer-links_work p:nth-child(2n + 1) {
  margin-bottom: 1rem;
}
.app__footer-links_icons {
  margin-top: 0.5rem;
}
.app__footer-links_icons svg {
  margin: 0.9rem;
  font-size: 30px;
  cursor: pointer;
}
.facebook {
  color: #4267b2;
}
.instagram {
  color: rgb(188, 42, 141);
}
.twitter {
  color: #00acee;
}
.facebook:hover {
  color: #4c7bd8;
}
.instagram:hover {
  color: rgb(171, 85, 144);
}
.twitter:hover {
  color: #1f62e8;
}
.app__footer-headtext {
  font-family: var(--font-base);
  color: var(--color-golden);
  font-weight: 400;
  letter-spacing: 0.04rem;
  font-size: 32px;
  line-height: 41.6px;
  margin-bottom: 1rem;
  text-transform: capitalize;
}
.footer__copyright {
  margin-top: 3rem;
}
@media screen and (min-width: 2000px) {
    .app__footer-headtext{
        font-size: 51px;
        line-height: 61px;
    }
}
@media screen and (max-width: 1150px) {
  .app__footer-links {
    align-items: center;
    flex-direction: column;
    padding: 0;
  }
  .app__footer-links_contact,
  .app__footer-links_logo,
  .app__footer-links_work {
    margin: 2rem 0;
    width: 100%;
  }
}
@media screen and (max-width: 650px) {
  .app__footer {
    padding: 0 0 2rem 0;
  }
}
@media screen and (max-width: 300px) {
  .app__footer-links_logo img:first-child {
    width: 80%;
  }
}
